import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/components/index'                                          // 
import Venue from '@/components/venue'                                          // 
import Venuedetail from '@/components/venuedetail'                              // -
import DigitalVenue from '@/components/digitalvenue'                            // 
import DigitalVenueList from '@/components/digitalvenuelist'                    // -
import DigitalVenueDetail from '@/components/digitalvenuedetail'                // -
import PersonalCenter from '@/components/personalcenter'                        // 
import PoliticsClass from '@/components/politicsclass'                          // 
import PoliticsClassMore from '@/components/politicsclassmore'                  // -
import PoliticsClassList from '@/components/politicsclasslist'                  // -
import PoliticsClassMoreDetail from '@/components/politicsclassmoredetail'      // --
import PoliticsClassNews from '@/components/politicsclassnews'                  // -
import PoliticsClassNewsDetail from '@/components/politicsclassnewsdetail'      // --
import GroupExhibition from '@/components/groupexhibition'                      // 
import GroupExhibitionMore from '@/components/groupexhibitionmore'              // -
import GroupExhibitionNews from '@/components/groupexhibitionnews'              // -
import GroupExhibitionDetail from '@/components/groupexhibitiondetail'          // --
import User from '@/components/user'                                            // 

Vue.use(Router)

let kejianrouter = new Router({
  // mode: 'history',
  routes: [
    {
      path: '*',
      name: 'Index',
      component: Index,
    },
    {
      path: '/',
      name: 'Index',
      component: Index,
    },
    {
      path: '/venue',
      name: 'venue',
      component: Venue,
    },
    {
      path: '/venuedetail',
      name: 'venuedetail',
      component: Venuedetail,
    },
    {
      path: '/digitalvenue',
      name: 'digitalvenue',
      component: DigitalVenue,
    },
    {
      path: '/digitalvenuelist',
      name: 'digitalvenuelist',
      component: DigitalVenueList,
    },
    {
      path: '/digitalvenuedetail',
      name: 'digitalvenuedetail',
      component: DigitalVenueDetail,
    },
    {
      path: '/personalcenter',
      name: 'personalcenter',
      component: PersonalCenter,
      meta: {
        auth: true,
      },
    },
    {
      path: '/politicsclass',
      name: 'politicsclass',
      component: PoliticsClass,
    },
    {
      path: '/politicsclassmore',
      name: 'politicsclassmore',
      component: PoliticsClassMore,
    },
    {
      path: '/politicsclasslist',
      name: 'politicsclasslist',
      component: PoliticsClassList,
    },
    {
      path: '/politicsclassmoredetail',
      name: 'politicsclassmoredetail',
      component: PoliticsClassMoreDetail,
    },
    {
      path: '/politicsclassnews',
      name: 'politicsclassnews',
      component: PoliticsClassNews,
    },
    {
      path: '/politicsclassnewsdetail',
      name: 'politicsclassnewsdetail',
      component: PoliticsClassNewsDetail,
    },
    {
      path: '/groupexhibition',
      name: 'groupexhibition',
      component: GroupExhibition,
    },
    {
      path: '/groupexhibitionmore',
      name: 'groupexhibitionmore',
      component: GroupExhibitionMore,
    },
    {
      path: '/groupexhibitionnews',
      name: 'groupexhibitionnews',
      component: GroupExhibitionNews,
    },
    {
      path: '/groupexhibitiondetail',
      name: 'groupexhibitiondetail',
      component: GroupExhibitionDetail,
    },
    {
      path: '/user',
      name: 'user',
      component: User,

    },
  ]
});
//  
kejianrouter.beforeEach((to, from, next) => {
  // 
  if (to.matched.some((res) => res.meta.requireAuth) || to.meta.auth) {
    // 
    if (localStorage.getItem("token")) {
          next();
    }else {
      // 
      next({
        path: "/user",
        query: {
          redirect: to.fullPath,
        },
      });
    }
  } else {
    next();
  }
});
export default kejianrouter;



