                </el-form-item>
              </el-col>
              <!--          <el-col :span="24" class="flex flex-row flex-nowrap align-start justify-between">
                <el-form-item label="" prop="vcode" :rules="[{required:true,message:''}]">
                  <el-input v-model="loginForm.vcode" placeholder=""></el-input>
                </el-form-item>
                <div class="formvcode"><img src="../assets/img/yzm.jpg"></div>
              </el-col> -->
