        </div>
        <!--  -->
        <div class="registerform" v-if="isRegister">
          <el-form ref="registerForm" :rules="registerrules" :model="registerForm" label-width="0px">
            <el-row>
              <el-col :span="24">
                <el-form-item label="" prop="tel">
                  <el-input v-model="registerForm.tel" placeholder=""></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="24" class="flex flex-row flex-nowrap align-start justify-between">
                <el-form-item label="" prop="sendCode" >
                  <el-input v-model="registerForm.sendCode" placeholder=""></el-input>
                </el-form-item>
                <div class="yam-btn">
                  <button @click="sendMsg" v-if="sendDisabled"></button>
                  <button disabled="true" v-else>{{sendTime+"s"}}</button>
                </div>
              </el-col>
              <el-col :span="24">
                <el-form-item label="" prop="password">
                  <el-input v-model="registerForm.password" type="password" placeholder="8-20//2"
                    show-password minlength="8" maxlength="20"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="24">
                <el-form-item label="" prop="againpassword">
                  <el-input v-model="registerForm.againpassword" type="password" placeholder="" show-password
                    minlength="8" maxlength="20"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="24">
                <el-checkbox v-model="checked"><span @click="openUserBox"></span><span @click="openUserBox"></span></el-checkbox>
              </el-col>
            </el-row>
            <el-form-item class="formbtn" v-if="!no">
              <el-button @click="saveRegister"></el-button>
            </el-form-item>

            <el-form-item class="formbtn" v-else>
              <el-button @click="saveRegister"></el-button>
            </el-form-item>
          </el-form>
        </div>
      </div>
