              <p></p>
              <p></p>
              <p></p>
              <p></p>
              <p></p>
              <p></p>
              <p></p>
              <p>IPMACIMEI/android
                ID/IDFA/OPENUDID/GUIDSIMIMSI</p>
            </el-scrollbar>
          </div>
          <div class="dingzhi-close" @click="isUserBox = false"><i class="el-icon-close text-c72c2c"></i></div>
        </div>
      </div>
    </div>

    <div class="new_footer">
      <div class="new_foot">
        <div class="new_foot_left">
          <ul>
            <li>
              <router-link to="/index"></router-link>
            </li>
            <li>|</li>
            <li>
              <router-link to="/groupexhibition"></router-link>
            </li>
            <li>|</li>
            <li>
              							<router-link to="/groupexhibitionnews"></router-link>
            </li>
