</template>

<script>
  import api from "../api/api.js";
  import {
    Message
  } from 'element-ui';
  export default {
    name: 'user',
    data() {
      var validatePass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(''));
        } else if (value !== this.registerForm.password) {
          callback(new Error('!'));
        } else {
          callback();
        }
      };
      return {
        isLogin: true,
        isRegister: false,
        loginForm: {
          name: '',
          password: '',
          // vcode: ''
        },
        registerForm: {
          tel: '',
          sendCode: '',
          password: '',
          againpassword: '',
          vcode: ''
        },
        registerrules: {
          tel: [{
            required: true,
            message: ''
          }],
          sendCode: [{
            required: true,
            message: ''
          }],
          password: [{
            required: true,
            message: ''
          }, {
            min: 8,
            max: 20,
            message: ' 8  20 '
          }],
          againpassword: [{
            min: 8,
            max: 20,
            message: ' 8  20 '
          }, {
            validator: validatePass,
            trigger: 'blur'
          }],
        },
        sendDisabled: true, // 
        sendTime: 60, // 
        checked: false, // 
        isUserBox: false, // 
        no:false
      }
    },
    mounted() {

    },
    methods: {
      tourl(url){
        let routeUrl = this.$router.resolve({
                  path:url,
                }) // 

                window.open(routeUrl.href, '_blank');
      },
      gowj(){
        this.no = true
        this.isRegister =true
      },
      // 
      sendMsg: function() {
        let that = this;
        if (that.registerForm.tel === '') {
          Message({
            message: '',
            type: 'error',
            center: true,
            offset: 130,
            duration: 1000
          });
          return false;
        } else if (!(/^1[3456789]\d{9}$/.test(that.registerForm.tel))) {
          Message({
            message: '',
            type: 'error',
            center: true,
            offset: 130,
            duration: 1000
          });
          return false;
        }
        if (that.sendDisabled == false) {
          return false
        } else {
          that.send()
        }
        that.countdown()
        that.sendDisabled = false;
      },

      // 
      saveLogin() {
        this.$refs['loginForm'].validate((valid) => {
          if (valid) {
            var data = this.loginForm
            this.$http
              .post(api.user.UserLogin, {
                username: data.name,
                password: data.password
              })
              .then((res) => {
                if (res.data.code == 0) {
                  localStorage.setItem("token", res.data.data.token);
                   localStorage.setItem("userinfo", JSON.stringify(res.data.data.userinfo));
                  if (localStorage.getItem("token")) {
                    this.$router.push({
                      path: '/index'
                    });
                  }
                } else {
                  this.$message({
                    message: res.data.msg,
                    type: "error",
                  });
                }
              });
          } else {
            return false
          }
        })
      },
    gb(){
      this.isRegister = !this.isRegister,
      this.no = false
       this.$refs['registerForm'].resetFields()
    },

      // 
      saveRegister() {

        this.$refs['registerForm'].validate((valid) => {
          if (valid) {
            if (!this.checked) {
              this.$message({
                message: '',
                type: "error",
              });
              return false
            }
             var data = this.registerForm
             console.log(this.registerForm,"jhh")
            if(this.no){
              var that = this
              this.$http
                .post(api.user.ForgetPassword, {
                  phone: that.registerForm.tel,
                  setPasscode: that.registerForm.sendCode,
                  password:that.registerForm.password,
                  reqirPass:that.registerForm.againpassword,
                })
                .then((res) => {
                  if (res.data.code == 0) {
                        this.$message({
                          message: '',
                          type: "success",
                        });
                        that.no = false
                        that.isRegister = false
                        that.$refs['registerForm'].resetFields()

                  } else {
                    this.$message({
                      message: res.data.res,
                      type: "error",
                    });
                  }
                });


              return false
            }
            this.$http
              .post(api.user.UserRegister, {
                password: data.password,
                phone: data.tel,
                affirmPass: data.againpassword,
                verifCode: data.sendCode,
              })
              .then((res) => {
                if (res.data.code == 0) {
                  localStorage.setItem("token", res.data.data.token);
                                     localStorage.setItem("userinfo", JSON.stringify(res.data.data.userInfo));
                  if (localStorage.getItem("token")) {
                    this.$router.push({
                      path: '/index'
                    });
                  }
                } else {
                  this.$message({
                    message: res.data.msg,
                    type: "error",
                  });
                }
              });
          } else {
            return false
          }
        })
      },
      // 
      send() {
        var name = ''
        if(!this.no){
          name = 'register'
        }else{
          name = 'setPass'
        }
        var that = this
        this.$http
          .post(api.user.SendMsgCode, {
            phone: that.registerForm.tel,
            scene: name
          })
          .then((res) => {
            if (res.data.code == 0) {
              this.$message({
                message: '',
                type: "success",
              });
            } else {
              this.$message({
                message: res.data.res,
                type: "error",
              });
            }
          });
      },
      // 
      openUserBox() {
        this.isUserBox = true;
      },
      // 
      countdown: function() {
        var promise = new Promise((resolve, reject) => {
          var setTimer = setInterval(() => {
            this.sendTime--
            if (this.sendTime <= 0) {
              this.sendTime = 60;
              this.sendDisabled = true
              resolve(setTimer)
            }
          }, 1000)
        })
        promise.then((setTimer) => {
          clearInterval(setTimer)
        })
      },
    },
  }
</script>

<style scoped>
  .user {
    width: 100vw;
    background-color: #fff;
  }
  ul li{
    cursor: pointer;
  }
  .user-left {
