    border-color: #cbcbcb;
  }

  .user-right>>>.el-form-item.is-error .el-input__inner {
    border-color: #F56C6C;
  }

  .formvcode {
    width: 140px;
    height: 52px;
    overflow: hidden;
  }

  .forgotpassword {
    height: 30px;
